import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import * as firebase from 'firebase'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    sideNav: false,
    subNavItems: [],
    navItems: [
      {
        name: 'Agenda',
        path: '/page/Agenda',
        menus: ['toolbar','drawer','home'],
        auth: ['all'],
        color: 'blue darken-4',
      },
      {
        name: 'Home',
        path: '/',
        menus: ['all'],
        auth: ['all'],
        color: 'blue-grey darken-3',
        fontColor: "white",
      },
      {
        name: 'Chat',
        path: '/chat',
        menus: ['drawer','home'],
        auth: ['all'],
        color: 'deep-orange darken-4',
      },
      {
        name: 'Documents',
        path: '/documents',
        menus: ['toolbar','drawer','home'],
        auth: ['all'],
        color: 'green darken-4',
      },
      {
        name: 'Pages',
        path: '/pages',
        menus: ['drawer','home'],
        auth: ['all'],
        color: 'blue darken-4',
      },
      {
        name: 'Teams',
        path: '/teams',
        menus: ['drawer','home'],
        auth: ['all'],
        color: 'purple darken-4',
      },
      {
        name: 'Structures',
        path: '/team/structures',
        menus: ['teamsnav'],
        auth: ['all'],
        color: 'purple darken-4',
      },
      {
        name: 'Membership',
        path: '/team/membership',
        menus: ['teamsnav'],
        auth: ['all'],
        color: 'purple darken-4',
      },
      {
        name: 'Finance',
        path: '/team/finance',
        menus: ['teamsnav'],
        auth: ['all'],
        color: 'purple darken-4',
      },
      {
        name: 'Positions',
        path: '/team//positions',
        menus: ['teamsnav'],
        auth: ['all'],
        color: 'purple darken-4',
      },
      {
        name: 'Admin',
        path: '/adminnav',
        menus: ['drawer','home'],
        auth: ['all'],
        color: 'blue-grey darken-2',
      },
      {
        name: 'item1',
        path: '/item1',
        menus: ['adminnav'],
        auth: ['all'],
        color: 'grey darken-3',
      },
      {
        name: 'item2',
        path: '/item2',
        menus: ['adminnav'],
        auth: ['all'],
        color: 'black',
      },
      {
        name: 'About',
        path: '/about',
        menus: ['all'],
        auth: ['all'],
        color: 'brown',
        fontColor: "white",
      },
    ],
    pages: [],
    documents: [],
    teamMembers: []
  },
  getters: {
    sideNav: state => state.sideNav,
    menuItems: state => state.menuItems,
    subNavItems: state => state.subNavItems,
    toolbarNavItems: function(state) {
      return state.navItems.filter(e => e.menus.includes('toolbar') || e.menus.includes('all'))
    },
    homeNavItems: function(state) {
      return state.navItems.filter(e => e.menus.includes('home') || e.menus.includes('all'))
    },
    drawerNavItems: function(state) {
      return state.navItems.filter(e => e.menus.includes('drawer') || e.menus.includes('all'))
    },
    adminNavItems: function(state) {
      return state.navItems.filter(e => e.menus.includes('adminnav'))
    },
    teamsNavItems: function(state) {
      return state.navItems.filter(e => e.menus.includes('teamsnav'))
    },
    pages: state => state.pages,
    documents: state => state.documents,
    teamMembers: state => state.teamMembers,
    filteredTeam: (state) => (tag) => {
      return state.teamMembers.filter( e => e.TAG === tag)
    },
    filteredTeamNotes: (state) => (teamName) => {
      return state.pages.filter( e => e.NAME.includes(teamName.substr(0,4)) )
    },
    // membershipTeamNotes: function(state) {
    //   return state.pages.filter( e => e.NAME.includes("Membership") )
    // },
    // financeTeam: function (state) {
    //   return state.teamMembers.filter( e => e.TAG === "fc_finance")
    // },
    // financeTeamNotes: function(state) {
    //   return state.pages.filter( e => e.NAME.includes("Finance") )
    // },
    // structuresTeam: function (state) {
    //   return state.teamMembers.filter( e => e.TAG === "fc_structures")
    // },
    // structuresTeamNotes: function(state) {
    //   return state.pages.filter( e => e.NAME.includes("Structure") )
    // },
    agenda: function(state) {
      return state.pages.filter( e => e.NAME.includes("Agenda") )
    }
  },
  mutations: {
    sideNav (state, payload) {
      state.sideNav = payload
    },
    subNavItems (state, payload) {
      state.subNavItems = payload
    },
    membershipTeamNotes (state, payload) {
      state.membershipTeamNotes = payload
    },
    pages ( state, payload ) {
      state.pages = payload
    },
    documents ( state, payload ) {
      state.documents = payload
    },
    teamMembers ( state,payload ) {
      state.teamMembers = payload
    }
  },
  actions: {
    getPages ( context ) {
      axios
      .get('http://127.0.0.1:8000/api/fellowshipcouncil/pages')
      .then( response => {
        console.log(response.data)
        context.commit("pages", response.data)
      })
    },
    getDocuments ( context ) {
      axios
      .get('http://127.0.0.1:8000/api/fellowshipcouncil/documents')
      .then( response => {
        console.log(response.data)
        context.commit("documents", response.data)
      })
    },
    getTeamMembers ( context ) {
      axios
      .get('http://127.0.0.1:8000/api/fellowshipcouncil/teammembers')
      .then( response => {
        console.log(response.data)
        context.commit("teamMembers", response.data)
      })
    }

  }
})
