import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

const About = () => import('./views/About.vue')
const Teams = () => import('./views/Teams.vue')
const Team = () => import('./views/Team.vue')
// const Structures = () => import('./views/Structures.vue')
// const Membership = () => import('./views/Membership.vue')
// const Finance = () => import('./views/Finance.vue')
// const Positions = () => import('./views/Positions.vue')
const Announcements = () => import('./views/Announcements.vue')
const Chat = () => import('./views/Chat.vue')
const Pages = () => import('./views/Pages.vue')
const Page = () => import('./views/Page.vue')
const Test = () => import('./views/Test.vue')
const Documents = () => import('./views/Documents.vue')
const DocumentsDialog = () => import('./views/DocumentsDialog.vue')
const AdminNav = () => import('./views/AdminNav.vue')

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/adminnav',
      name: 'adminnav',
      component: AdminNav
    },
    // {
    //   path: '/teamsnav',
    //   name: 'teamsnav',
    //   component: TeamsNav
    // },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/team/:tag',
      name: 'team',
      component: Team
    },
    {
      path: '/teams',
      name: 'teams',
      component: Teams
    },
    // {
    //   path: '/structures',
    //   name: 'structures',
    //   component: Structures
    // },
    // {
    //   path: '/membership',
    //   name: 'membership',
    //   component: Membership
    // },
    // {
    //   path: '/finance',
    //   name: 'finance',
    //   component: Finance
    // },
    // {
    //   path: '/positions',
    //   name: 'positions',
    //   component: Positions
    // },
    {
      path: '/announcements',
      name: 'announcements',
      component: Announcements
    },
    {
      path: '/chat',
      name: 'chat',
      component: Chat
    },
    {
      path: '/documents',
      name: 'documents',
      component: Documents
    },
    {
      path: '/pages',
      name: 'pages',
      component: Pages
    },
    {
      path: '/page/:id',
      name: 'page',
      component: Page
    },
    {
      path: '/test/:testid',
      name: 'test',
      component: Test
    },
    {
      path: '/documentsDialog',
      name: 'documentsDialog',
      component: DocumentsDialog
    }
  ]
})
