<template>
<div>

    <v-toolbar>
      <v-toolbar-side-icon @click="toggleSideNav"></v-toolbar-side-icon>
      <v-toolbar-title>Fellowship Council</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-sm-and-down">
        <v-btn flat v-for="item in toolbarNavItems" :key=item.name v-html="item.name" @click="goToPath(item.path)">
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>

</div>    

</template>

<script>
export default {
  methods: {
    toggleSideNav () {
      this.$store.commit("sideNav", !this.sideNav)
    },
    goToPath (path) {
      this.$router.push(path)
    }
  },
  computed: {
    sideNav: {
      get () { return this.$store.getters.sideNav },
      set (value) { this.$store.commit("sideNav", value)}
    },
    toolbarNavItems: function() {
        return this.$store.getters.toolbarNavItems
    },
  }
}
</script>

