<template>
<div>

    <v-navigation-drawer
      v-model="sideNav" 
      temporary 
      absolute
    >

      <v-toolbar flat class="transparent">
        <v-list class="pa-0">
          <v-list-tile avatar>
            <v-list-tile-avatar>
              <img src="https://charisfellowship.us/assets/img/logo/CHARIS-005-ColorFlagsSymbol_100x100.png">
            </v-list-tile-avatar>

            <v-list-tile-content>
              <v-list-tile-title>Charis Fellowship</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-toolbar>

      <v-list class="pt-0" dense>
        <v-divider></v-divider>
          <template
            v-for="item in drawerNavItems"
          >

            <v-list-tile
              :key="item.name"
              router :to="item.path"        
              >
              <v-list-tile-content>
                <v-list-tile-title>{{ item.name }}</v-list-tile-title>
              </v-list-tile-content>


            </v-list-tile>

          </template>

      </v-list>

    </v-navigation-drawer>  

</div>
  
</template>

<script>
export default {
  data () {
    return {
      message: "Nav Bar",
    }
  },
  methods: {
    goToSubNav(subNavItems){
      this.$store.commit('subNavItems', subNavItems)
      this.$router.push('SubNav')
    }
  },
  computed: {
    sideNav: {
      get () { return this.$store.getters.sideNav },
      set (value) { this.$store.commit("sideNav", value)}
    },
    drawerNavItems: function() {
        return this.$store.getters.drawerNavItems
    },
  }
}
</script>
